import React from 'react';
import FullLayout from '../../layout/fullLayout';
import background from '../../asserts/background.svg';
import './index.scss';
import { Form, Input, Button, Checkbox, Tabs, Row } from 'antd';
import {
  UserOutlined,
  LockOutlined,
  AlipayCircleFilled,
  WeiboCircleFilled,
  TaobaoCircleFilled
} from '@ant-design/icons';
import logo from '../../asserts/logo.svg';
import Footer from '../../components/footer';
interface IPasswordForm {
  username: string;
  phone: string;
}
interface IPhoneForm {
  phone: string;
  captcha: string;
}
const { TabPane } = Tabs;
const Login = () => {
  return (
    <FullLayout backgroundImg={background}>
      <div className="login__container">
        <Row align="middle" justify="center">
          <img alt="" className="login__logo" src={logo} />
          <span className="login__title">Ant Design</span>
        </Row>
        <Row align="middle" justify="center">
          <span className="login__tip">Ant Design  Web </span>
        </Row>
        <Row align="middle" justify="center">
          <Tabs defaultActiveKey="1">
            <TabPane tab="" key="1">
              <PasswordLogin />
            </TabPane>
            <TabPane tab="" key="2">
              <PhoneLogin />
            </TabPane>
          </Tabs>
        </Row>
        <Footer />
      </div>
    </FullLayout>
  );
};
const PhoneLogin = () => {
  return (
    <Form name="phoneLogin" initialValues={{ remember: true }} onFinish={passwordFormFinish}>
      <Form.Item name="phone" rules={[{ required: true, message: '' }]}>
        <Input
          className="phone__input"
          size="large"
          prefix={<UserOutlined className="site-form-item-icon" />}
          placeholder=""
        />
      </Form.Item>
      <Form.Item name="captcha" rules={[{ required: true, message: '!' }]}>
        <Row justify="space-between">
          <Input
            prefix={<LockOutlined className="site-form-item-icon" />}
            type="password"
            placeholder=""
            className="captcha__input"
            size="large"
          />
          <Button size="large"></Button>
        </Row>
      </Form.Item>
      <Form.Item>
        <Row justify="space-between">
          <Form.Item name="remember" valuePropName="checked" noStyle>
            <Checkbox></Checkbox>
          </Form.Item>
          <a href=""></a>
        </Row>
      </Form.Item>
      <Form.Item>
        <Button className="password__button" size="large" type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
      <Form.Item>
        <Row justify="space-between">
          <Form.Item label="" colon={false} className="login__iconGroup">
            <AlipayCircleFilled className="login__icon" />
            <WeiboCircleFilled className="login__icon" />
            <TaobaoCircleFilled className="login__icon" />
          </Form.Item>
          <a href=""></a>
        </Row>
      </Form.Item>
    </Form>
  );
};
const PasswordLogin = () => {
  return (
    <Form name="passwordLogin" initialValues={{ remember: true }} onFinish={phoneFormFinish}>
      <Form.Item name="username" rules={[{ required: true, message: '' }]}>
        <Input
          size="large"
          className="password__input"
          prefix={<UserOutlined className="site-form-item-icon" />}
          placeholder="admin or user"
        />
      </Form.Item>
      <Form.Item name="password" rules={[{ required: true, message: '!' }]}>
        <Input
          prefix={<LockOutlined className="site-form-item-icon" />}
          type="password"
          placeholder="123456"
          className="password__input"
          size="large"
        />
      </Form.Item>
      <Form.Item>
        <Row justify="space-between">
          <Form.Item name="remember" valuePropName="checked" noStyle>
            <Checkbox></Checkbox>
          </Form.Item>
          <a href=""></a>
        </Row>
      </Form.Item>
      <Form.Item>
        <Button size="large" className="password__button" type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
      <Form.Item>
        <Row justify="space-between">
          <Form.Item label="" colon={false} className="login__iconGroup">
            <AlipayCircleFilled className="login__icon" />
            <WeiboCircleFilled className="login__icon" />
            <TaobaoCircleFilled className="login__icon" />
          </Form.Item>
          <a href=""></a>
        </Row>
      </Form.Item>
    </Form>
  );
};
const passwordFormFinish = (values: IPasswordForm) => {
  console.log('Received password form: ', values);
};
const phoneFormFinish = (values: IPhoneForm) => {
  console.log('Received phone form: ', values);
};
export default Login;
